import React, { useRef } from 'react';
import { useRender } from 'react-zdog';
import ShapeExtrusion from './ShapeExtrusion';

const spacing = (i, v) => ({ x: 0, y: Math.sin(v * Math.PI * 2 + Date.now() / 2000) * 20, z: i * 2 });
const sideColor = (v) => `rgb(${v * 255}, ${0}, ${0})`;

const AnimatedShapeExtrusion = (props) => {
    const ref = useRef(undefined);
    useRender(() => {
        ref.current.translate.x = Math.sin(Date.now() / 2000);
    });

    return (
        <ShapeExtrusion ref={ref} translate={{x:0,y:0,z:0}} spacing={spacing} sideColor={sideColor} {...props} />
    )
}

export default AnimatedShapeExtrusion;